.buttonWithoutIcon {
    display: flex;
    min-width: 50px;
    border: 1px solid #ddd;
    color: black;
    background-color: white;
    border-radius: 4px;
    margin: 0 2px;
    padding: 2px 3px;
    cursor: pointer;
    text-align: center;
    justify-content: flex-end;
    align-items: center;
}
